export const idle = {
    type: 0 /* Types.Idle */,
};
export function isIdle(state) {
    return state.type === 0 /* Types.Idle */;
}
export function setupError(error) {
    return {
        type: 1 /* Types.SetupError */,
        error,
    };
}
export function isSetupError(state) {
    return state.type === 1 /* Types.SetupError */;
}
export function loaded(data) {
    return {
        type: 2 /* Types.Loaded */,
        data,
    };
}
export function isLoaded(state) {
    return state.type === 2 /* Types.Loaded */;
}
export const canvas = {
    type: 3 /* Types.Canvas */,
};
export function isCanvas(state) {
    return state.type === 3 /* Types.Canvas */;
}
export const info = { type: 4 /* Types.Info */ };
export function isInfo(state) {
    return state.type === 4 /* Types.Info */;
}
export const preferences = { type: 5 /* Types.Preferences */ };
export function isPreferences(state) {
    return state.type === 5 /* Types.Preferences */;
}
